import React, { createContext, useState, useEffect } from 'react';
import { ThemeProvider as StyledThemeProvider } from 'styled-components';
import { GlobalStyle } from '../utils/theme';

// ÌÖåÎßà Ïª®ÌÖçÏä§Ìä∏ ÏÉùÏÑ±
export const ThemeContext = createContext({
  mode: 'light',
  toggleTheme: () => {},
});

// ÌÖåÎßà ÏÉÅÌÉú Í¥ÄÎ¶¨ Î∞è Provider Ïª¥Ìè¨ÎÑåÌä∏
export const ThemeProvider = ({ children }) => {
  // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú ÌÖåÎßà ÏÉÅÌÉú Î∂àÎü¨Ïò§Í∏∞ (Í∏∞Î≥∏Í∞í: light)
  const [themeMode, setThemeMode] = useState('light');

  useEffect(() => {
    // Î∏åÎùºÏö∞Ï†ÄÏóêÏÑúÎßå Ïã§ÌñâÎêòÎèÑÎ°ù Ï≤¥ÌÅ¨
    if (typeof window !== 'undefined') {
      const savedTheme = localStorage.getItem('theme');
      // Ï†ÄÏû•Îêú ÌÖåÎßàÍ∞Ä ÏûàÏúºÎ©¥ ÏÇ¨Ïö©, ÏóÜÏúºÎ©¥ ÏãúÏä§ÌÖú ÏÑ§Ï†ï ÌôïÏù∏
      if (savedTheme) {
        setThemeMode(savedTheme);
      } else if (
        window.matchMedia &&
        window.matchMedia('(prefers-color-scheme: dark)').matches
      ) {
        setThemeMode('dark');
      }
    }
  }, []);

  // ÌÖåÎßà ÌÜ†Í∏Ä Ìï®Ïàò
  const toggleTheme = () => {
    const newTheme = themeMode === 'light' ? 'dark' : 'light';
    setThemeMode(newTheme);
    // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê ÌÖåÎßà ÏÉÅÌÉú Ï†ÄÏû•
    if (typeof window !== 'undefined') {
      localStorage.setItem('theme', newTheme);
    }
  };

  // ÌÖåÎßà Ïª®ÌÖçÏä§Ìä∏ Í∞í
  const themeContext = {
    mode: themeMode,
    toggleTheme,
  };

  return (
    <ThemeContext.Provider value={themeContext}>
      <StyledThemeProvider theme={{ mode: themeMode }}>
        <GlobalStyle />
        {children}
      </StyledThemeProvider>
    </ThemeContext.Provider>
  );
};

export default ThemeProvider;
